<template>
  <Layout class="case-page">
    <Row class="tool-bar">
      <div class="left">
        <Input class="search-input" v-model="searchKey" placeholder="请输入关键字搜索" @keyup.native.enter="getCaseList">
          <Button slot="append" icon="md-search" @click.native="getCaseList"/>
        </Input>
      </div>
      <div class="right">
        <Button v-if="selectedCase.length" type="error" style="margin-right: 0.5rem" @click.native="deleteCase">删除</Button>
        <Button type="primary" @click="$router.push({ name: 'CaseDetail', params: { type: 'new', id: 0 } })">添加案例</Button>
      </div>
    </Row>
    <Row class="tags">
      <Tag color="success" @click.native="searchTag(null)">全部</Tag>
      <Tag color="success" v-for="(item, index) in tagList" :key="index" @click.native="searchTag(item)">{{item.tagName}}</Tag>
    </Row>
    <Row class="case-list">
      <Table class="case-table" :columns="columns" :data="caseList" @on-selection-change="selectCase"></Table>
      <!-- <Page :total="40" show-elevator show-sizer></Page> -->
    </Row>
    <Row class="pagination">
      <Page :total="paginationData.total" :current="paginationData.page" show-elevator show-sizer show-total @on-change="changePage" @on-page-size-change="changePageSize"></Page>
    </Row>
  </Layout>
</template>

<script>
import { getCaseList, deleteCase } from '@/api/case'
import { getTagesByRelation } from '@/api/knowledge'

export default {
  name: 'case',
  data () {
    return {
      searchKey: '',
      currentEditData: {},
      columns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          width: 60,
          render: (h, { row, column, index }) => {
            return h(
              'span',
              `#${index + 1}`
            )
          }
        },
        {
          title: '标题',
          key: 'title',
          render: (h, { row, column, index }) => {
            return h(
              'a',
              {
                attrs: {
                  href: 'javascript: void(0)'
                },
                on: {
                  click: () => {
                    this.openEditModal(row)
                  }
                }
              },
              row.title
            )
          }
        },
        {
          title: '标签',
          key: 'tag',
          render (h, data) {
            const tags = []
            data.row.tagInfo = data.row.tagInfo ? data.row.tagInfo : []
            for (let tag of data.row.tagInfo) {
              tags.push(h(
                'Tag',
                {
                  props: {
                    color: 'success'
                  }
                },
                tag.name
              ))
            }
            return h(
              'div',
              {
                attrs: {
                  class: 'case-tags'
                }
              },
              tags
            )
          }
        },
        {
          title: '内容',
          render: (h, { column, row, index }) => {
            return h(
              'span',
              {
                style: {
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap'
                }
              },
              row.content
            )
          }
        }
      ],
      caseList: [],
      paginationData: {
        total: 0,
        pageSize: 10,
        page: 1
      },
      selectedCase: [],
      tagList: [],
      selectedTag: null
    }
  },
  computed: {
    showEditModal: {
      get () {
        return JSON.stringify(this.currentEditData) !== '{}'
      },
      set (value) {
        if (!value) {
          this.currentEditData = {}
        }
      }
    }
  },
  created () {
    this.getCaseList()
    this.getTagList()
  },
  mounted () {},
  methods: {
    getTagList () {
      getTagesByRelation({
        type: 5
      }).then(({ data, response }) => {
        if (response.success) {
          this.tagList = data
        } else {
          this.$Message.error(response.msg)
        }
      })
    },
    openEditModal (data) {
      this.$router.push({ name: 'CaseDetail', params: { type: 'detail', id: data.id } })
    },
    getCaseList () {
      getCaseList({
        pageNo: this.paginationData.page,
        pageSize: this.paginationData.pageSize,
        title: this.searchKey || null,
        tagId: this.selectedTag ? this.selectedTag.tagId : null
      }).then(({ data, response }) => {
        if (response.success) {
          this.caseList = data.result
          this.paginationData.total = data.totalCount
        }
      })
    },
    selectCase (selection) {
      this.selectedCase = selection
    },
    deleteCase () {
      this.$Modal.confirm({
        title: '提示',
        content: '警告⚠️删除后数据不可恢复，请确定是否删除？',
        onCancel: () => {},
        onOk: () => {
          deleteCase({
            ids: this.selectedCase.map(item => item.id)
          }).then(({ data, response }) => {
            if (response.success) {
              this.$Message.success('删除成功！')
              this.getCaseList()
            } else {
              this.$Message.error(response.msg)
            }
          })
        }
      })
    },
    changePage (page) {
      this.paginationData.page = page
      this.getCaseList()
    },
    changePageSize (size) {
      this.paginationData.pageSize = size
      this.getCaseList()
    },
    searchTag (tag) {
      this.selectedTag = tag
      this.getCaseList()
    }
  }
}
</script>

