  background none
  height 100%
  .tool-bar
    flex 1 0 auto
    margin-bottom 0.5rem
    display flex
    .left
      flex 1 1 auto
      .search-input
        max-width 20rem
    .right
      flex 1 0 auto
      text-align right
  .tags
    margin-bottom 0.5rem
  .case-list
    flex 1 1 auto
    height 100%
    margin-bottom 0.5rem
    overflow hidden
    .case-table
      height 100%
      flex 1 1 auto
      overflow hidden
</style>

